"use client"

import { useRef, useEffect, useState } from "react"
import { Users, Music, Headphones, Globe } from "lucide-react"

const stats = [
  {
    icon: Users,
    value: 50,
    suffix: "M+",
    label: "Active Listeners",
    description: "Music lovers worldwide",
  },
  {
    icon: Music,
    value: 100,
    suffix: "M+",
    label: "Songs Available",
    description: "Every genre imaginable",
  },
  {
    icon: Headphones,
    value: 200,
    suffix: "+",
    label: "Curated Channels",
    description: "Expert-crafted playlists",
  },
  {
    icon: Globe,
    value: 190,
    suffix: "+",
    label: "Countries",
    description: "Global reach",
  },
]

function AnimatedCounter({ value, suffix, isVisible }: { value: number; suffix: string; isVisible: boolean }) {
  const [count, setCount] = useState(0)

  useEffect(() => {
    if (!isVisible) return

    let start = 0
    const end = value
    const duration = 2000
    const increment = end / (duration / 16)

    const timer = setInterval(() => {
      start += increment
      if (start >= end) {
        setCount(end)
        clearInterval(timer)
      } else {
        setCount(Math.floor(start))
      }
    }, 16)

    return () => clearInterval(timer)
  }, [value, isVisible])

  return (
    <span>
      {count}
      {suffix}
    </span>
  )
}

export function StatsSection() {
  const [isVisible, setIsVisible] = useState(false)
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.3 },
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => observer.disconnect()
  }, [])

  return (
    <section ref={sectionRef} className="py-20 bg-foreground text-background">
      <div className="mx-auto max-w-7xl px-6 lg:px-8">
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-8 lg:gap-12">
          {stats.map((stat, index) => (
            <div
              key={stat.label}
              className={`text-center transition-all duration-700 ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"}`}
              style={{ transitionDelay: `${index * 150}ms` }}
            >
              <div className="w-16 h-16 rounded-2xl bg-background/10 flex items-center justify-center mx-auto mb-6">
                <stat.icon className="w-8 h-8 text-primary" />
              </div>
              <p className="text-5xl md:text-6xl font-bold text-background mb-2">
                <AnimatedCounter value={stat.value} suffix={stat.suffix} isVisible={isVisible} />
              </p>
              <p className="text-lg font-medium text-background/90 mb-1">{stat.label}</p>
              <p className="text-sm text-background/50">{stat.description}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
